<template>
  <div :style="{display: visible ? 'block' : 'none'}">
    <div class="ays_main">
      <div class="ays_border">
        <div class="ays_circle"></div>
        <div class="ays_circle2"></div>
        <div class="ays_border2"></div>
      </div>
      <div class="ays_weaponlist">
        <div>
          <div v-for="(weapon, index) in weapons" :key="index" @click="select(index)" :class="index == selected ? 'ays_weapon selected' : 'ays_weapon'">
            <h1>{{weapon.label}}</h1>
            <img :src="imagePathFinder(weapon.name)">
          </div>
        </div>
      </div>
      <div class="ays_weaponinfos" v-if="weapons.length > 0">
        <h1>{{weapons[selected].label}}</h1>
        <div class="ays_weaponinfo_label">
          <div class="ays_weaponinfo_circle"></div>
          <img :src="imagePathFinder(weapons[selected].name)">
          <h1>{{weapons[selected].label}}</h1>
        </div>
        <div class="ays_specs">
          <div class="ays_spec" v-for="(stat, index) in weapons[selected].specs" :key="index">
            <p>{{stat.label}}</p>
            <div class="ays_spec_progs">
              <div class="ays_spec_prog_active"></div>
              <div :class="stat.state > 1 ? 'ays_spec_prog_active' : 'ays_spec_prog'"></div>
              <div :class="stat.state > 2 ? 'ays_spec_prog_active' : 'ays_spec_prog'"></div>
              <div :class="stat.state > 3 ? 'ays_spec_prog_active' : 'ays_spec_prog'"></div>
              <div :class="stat.state > 4 ? 'ays_spec_prog_active' : 'ays_spec_prog'"></div>
              <p>{{(stat.state > 5) ? '5/5' : (0 > stat.state) ? '1/5' : stat.state + '/5'}}</p>
            </div>
          </div>
        </div>
        <div class="ays_needscraft">
          <div class="ays_needs">
            <p>Benötigt:</p>
            <div class="neededitem" v-for="(need, index) in weapons[selected].needs" :key="index" :tooltip="need.label">
              <img :src="imagePathFinder(need.name)">
              <p>{{'x ' + need.amount}}</p>
            </div>
          </div>
          <button type="button" class="ays_craft" @click="herstellen()">Herstellen</button>
        </div>
      </div>
      <button type="button" class="ays_close" @click="close()"><i class="far fa-close"></i></button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ays_waffenverarbeiter',
  data() {
    return {
      visible: false,
      selected: 0,
      weapons: []
    }
  },
  methods: {
    imagePathFinder(file){
      return "./img/" + file + ".png"
      //return require('@/assets/' + file + '.png')
    },
    close() {
      this.visible = false
      this.selected = 0
      fetch("https://ays_weaponcraft/escape", {
        method: "post"
      })
    },
    select(index) {
      this.selected = index
    },
    herstellen() {
      fetch("https://ays_weaponcraft/craft", {
        method: "post",
        body: JSON.stringify({
          selected: this.selected
        })
      })
    }
  },
  mounted() {
    window.addEventListener('message', (e) => {
      var event = e.data
      if (event.action == "show") {
        if (event.state) {
          this.visible = true
        } else {
          this.close()
        }
      } else if (event.action == "setweapons") {
        this.weapons = JSON.parse(event.data)
      }
    });
  }
}
</script>

